import mInput from '../../components/m-input.vue'
import pwsInput from '../../components/input-password.vue'
import mButton from '../../components/m-button.vue'
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex';
// import Login from '../../api.js'
import crypto from 'crypto'
import {
	getClentId
} from '@/common/js/getCLentId.js'
export default {
	data() {
		return {
			placeholder: "用户名/手机号",
			placeholderPws: "请输入密码",
			btnType: "primary",
			btnDisabled: false,
			btnValue: "登录",
			consumer: {
				username: '',
				password: '',
			},
			status: '',
			clientId: '',
		}
	},
	onLoad(option) {
		// console.log(getCurrentPages())
		this.status = option.val;
		this.showToasts();
		this.getclientId();
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		...mapActions(['handeLogin']),

		getclientId() {
			// 扩展API加载完毕，现在可以正常调用扩展API
			// 获取客户端标识信息
















		},
		handleLogin() {
			this.getclientId();
			// getClentId();
			// var md5 = crypto.createHash("md5");
			// md5.update(password); //需要加密的密码
			// const passwd = md5.digest("hex"); //password 加密完的密码
			if (this.consumer.username != "" && this.consumer.username != null) {
				if (this.consumer.password != "" && this.consumer.password != null) {
					const that = this;
					this.test.post('base/login', {
						username: this.consumer.username,
						passwd: this.consumer.password,
						client_id: this.clientId,
					}).then(res => {
						console.log(res)
						const data = res.data;
						const datas = data.value
						if (data.errorCode == 0 && res.statusCode == 200) {
							console.log(res)
							uni.setStorage({
								key: 'Token',
								data: res.data.value.token
							})
							this.handeLogin(datas);
							uni.switchTab({
								url: '../index/index'
							})
						} else {
							uni.showToast({
								"title": data.message,
								"icon": "none"
							})
						}
					}).catch(err => {
						console.log(err)
						uni.showToast({
							title: err.errMsg,
							icon: 'none'
						})
					})

				} else {
					return uni.showToast({
						icon: "none",
						title: "密码不能为空"
					})
				}
			} else {
				return uni.showToast({
					icon: "none",
					title: "账号不能为空"
				})
			}
		},
		showToasts() {
			if (this.status == 1) {
				uni.showToast({
					title: '注册已提交，审核成功后即可登录使用',
					position: 'bottom',
					icon: 'none'
				});
			}

		}
	},
	...mapMutations(['login']),

	components: {
		mInput,
		pwsInput,
		mButton
	}
}
